<template>
   <div>
      <basic-header :items="['系统管理','密码修改']"></basic-header>
      <el-card>
         <el-col :span="12">
         <el-form label-width="80px" :model="form" :rules="rules" ref="form">
            <el-form-item label="旧密码" prop="password">
               <el-input type="password" v-model="form.password"></el-input>
            </el-form-item>
            <el-form-item label="新密码" prop="newpassword1">
               <el-input type="password" v-model="form.newpassword1"></el-input>
            </el-form-item>
            <el-form-item label="确认密码" prop="newpassword2">
               <el-input type="password" v-model="form.newpassword2"></el-input>
            </el-form-item>
            <el-form-item>
               <el-button type="primary" @click="change">确认修改</el-button>
            </el-form-item>
         </el-form>
         </el-col>
      </el-card>
   </div>
</template>
<script>
    // @  src
    import Header from "@/components/Header.vue"
    export default {
        data(){
            let v1=(rule,value,callback)=>{
               let reg=/^[0-9a-zA-Z]{6,}$/;
               if(reg.test(value)){
                   callback();
               }else{
                   callback(new Error("密码必须由6位以上的字母或数字组成"));
               }
            };
            let v2=(rule,value,callback)=>{
                if(value===this.form.newpassword1){
                  callback()
                }else{
                  callback(new Error("两次密码输入不一致"));
                }
            };
            return {
                form:{
                    username:sessionStorage.getItem("user"),
                    password:"",
                    newpassword1:"",
                    newpassword2:""
                },
                rules:{
                    password:{required:true,message:"请输入旧密码",trigger:"blur"},
                    newpassword1:[
                        {required:true,message:"请输入新密码",trigger:"blur"},
                        {validator:v1,trigger:"blur"}
                    ],
                    newpassword2:[
                        {required:true,message:"请重复新密码",trigger:"blur"},
                        {validator:v2,trigger:"blur"}
                    ]
                }
            }
        },
        methods: {
            change(){
                this.$refs.form.validate(valid=>{
                    if(valid){
                        this.axios.put("/api/admin/changePassword",this.form).then(res=>{
                            if(res.data.code===200){
                                this.$message.success("修改成功");
                                this.$refs.form.resetFields();
                            }else{
                                this.$message.error(res.data.msg);

                            }
                        })
                    }else{
                        this.$message.error("验证失败");
                    }
                })
            }
        },
        components:{
          "basic-header":Header
        },
        created(){

        },
        mounted(){

        }
    }
</script>
